h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Montserrat", sans-serif;
}

header {
  padding: 22px 0;
  z-index: 997;
  transition: all 0.5s;
}
header.header-scrolled {
  padding: 12px 0;
  background-color: #ffffff;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
}
header .navbar {
  padding: 0;
}
header .navbar ul {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
header .navbar ul li {
  position: relative;
  padding: 10px 0 10px 40px;
}
header .navbar ul li a {
  padding: 0;
  font-size: 21px;
  color: #fff;
  opacity: 0.78;
  white-space: nowrap;
  transition: 0.3s;
}
header .navbar ul li a:hover, header .navbar ul li a:focus, header .navbar ul li a.active, header .navbar ul li a.active:focus {
  color: #fff;
  opacity: 1;
  text-shadow: 0.1px 0.1px 0.1px #fff;
}
header .navbar ul li:hover > a {
  color: #fff;
  opacity: 1;
}
header .navbar .mobile-nav-toggle {
  cursor: pointer;
  display: none;
  transition: 0.5s;
}
header .navbar .mobile-nav-toggle .nav-toggle-line {
  display: block;
  height: 2px;
  width: 20px;
  background-color: rgba(255, 255, 255, 0.78);
  transition: all 0.3s ease-in-out;
}
header .navbar .mobile-nav-toggle .nav-toggle-line:nth-child(2), header .navbar .mobile-nav-toggle .nav-toggle-line:last-child {
  margin-top: 6px;
}
header .navbar .mobile-nav-toggle.is-active .nav-toggle-line:first-child {
  transform: rotate(45deg);
}
header .navbar .mobile-nav-toggle.is-active .nav-toggle-line:nth-child(2) {
  opacity: 0;
}
header .navbar .mobile-nav-toggle.is-active .nav-toggle-line:last-child {
  transform: rotate(-45deg);
  margin-top: -10px;
}
header .navbar.navbar-mobile {
  position: fixed;
  overflow: hidden;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(9, 9, 9, 0.9);
  transition: 0.3s;
  z-index: 999;
}
header .navbar.navbar-mobile .mobile-nav-toggle {
  position: absolute;
  top: 25px;
  right: 25px;
}
header .navbar.navbar-mobile ul {
  display: block;
  position: absolute;
  top: 55px;
  right: 25px;
  left: 25px;
  padding: 10px 0;
  border-radius: 8px;
  background-color: #fff;
  overflow-y: auto;
  transition: 0.3s;
}
header .navbar.navbar-mobile ul li {
  padding: 0;
}
header .navbar.navbar-mobile ul li a {
  padding: 10px 20px;
  color: #ffffff;
}
header .navbar.navbar-mobile ul li a:hover, header .navbar.navbar-mobile ul li a.active {
  color: #9e801c;
  text-shadow: 0.2px 0.2px 0.2px #9e801c;
}

footer {
  border-bottom: 10px solid #9e801c;
}
footer .border-right--white:after {
  content: "";
  position: absolute;
  top: -10%;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  width: 1px;
  background: rgba(255, 255, 255, 0.5);
}
footer h3, footer .h3 {
  margin-top: 60px;
  margin-bottom: 30px;
  font-size: 38px;
}
footer address {
  font-size: 22px;
}
footer .contact-info-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
}
footer .contact-info-wrapper .contact-icon {
  flex: 0 0 40px;
  height: auto;
  margin-right: 20px;
}
footer .contact-info-wrapper .contact-icon img {
  max-width: 100%;
  height: 100%;
  object-fit: contain;
}
footer .contact-info-wrapper a {
  font-size: 29px;
  color: #fff;
}
footer .contact-info-wrapper a:hover {
  opacity: 0.7;
}
footer .contact-info-wrapper p {
  font-size: 17px;
  margin: 0;
}
footer .contact-info-wrapper:first-of-type {
  margin-top: 30px;
}
footer .text-financed {
  font-size: 29px;
}
footer .btn-primary {
  width: 235px;
}

@media (max-width: 991.98px) {
  footer h3, footer .h3 {
    font-size: 24px;
  }
  footer address {
    font-size: 16px;
  }
  footer .contact-info-wrapper {
    margin-bottom: 20px;
  }
  footer .contact-info-wrapper a,
footer .contact-info-wrapper p {
