.btn {
  line-height: 1.1;
  transition: all 0.3s ease-in;
}
.btn.btn-primary {
  font-size: 28px;
  color: #fff;
  padding: 20px 15px;
  border-radius: 0;
  font-family: "Montserrat", sans-serif;
}
.btn.btn-primary:hover {
  background-color: #ffffff;
  border-color: #ffffff;
}
.btn.btn-primary.is-rounded {
  font-size: 20px;
  padding: 20px 40px;
  border-radius: 18px;
  letter-spacing: 5px;
  text-transform: uppercase;
}
.btn.btn-body {
  color: #fff;
  background-color: #ffffff;
  border-color: #ffffff;
  border-radius: 0;
  font-size: 23px;
  padding: 10px 20px;
}
.btn.btn-body:hover {
  background-color: #9e801c;
  border-color: #9e801c;
}
@media (max-width: 991.98px) {
  .btn.btn-primary, .btn.btn-primary.is-rounded, .btn.btn-body {
    font-size: 18px;
  }
  .btn.btn-primary.is-rounded {
    padding: 20px 25px;
  }
}

